import PropTypes from "prop-types";
import React, { Component } from "react";
import Select from "react-select";
import "./react-select.css";
const CLOTHECATEGORIES = require("../../../../data/clothecategories");

class Dropdown extends Component {
  constructor() {
    super();
    this.state = {
      clotheType: "clothetype",
      selectValue: "Saree",
      clearable: true,
      searchable: true,
      disabled: false
    };
    this.updateValue = this.updateValue.bind(this);
  }
  getDefaultProps() {
    return {
      label: "ClotheType:",
      searchable: true
    };
  }

  getInitialState() {
    return {
      disabled: false,
      searchable: this.props.searchable,
      selectValue: "Saree",
      clearable: true
    };
  }
  clearValue(event) {
    this.select.setInputValue("");
  }

  switchClotheType(event) {
    var newClotheType = event.target.value;
    this.setState({
      clotheType: newClotheType,
      selectValue: null
    });
  }

  updateValue(newValue) {
    console.log(newValue);
    this.setState({ selectValue: newValue });
  }
  render() {
    var options = CLOTHECATEGORIES[this.state.clotheType];
    return (
      <div className="section">
        <h3 className="section-heading">Clothetype</h3>
        <Select
          id="clothetype-select"
          options={options}
          clearable={this.state.clearable}
          name="selectValue"
          value={this.state.selectValue}
          onChange={this.updateValue}
          searchable={this.state.searchable}
        />
      </div>
    );
  }
}

Dropdown.propTypes = {
  label: PropTypes.string,
  searchable: PropTypes.bool
};

export default Dropdown;



